import React, { useState } from "react";
import axios from "axios";
import { API_URL } from "@/api/api_URL";
import { useDispatch } from "react-redux";
import { login } from "@/Features/auth/authSlice";
import { useRouter } from "next/router";

const LoginPage = () => {
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [message, setMessage] = useState("");
  const [error, setError] = useState("");
  const dispatch = useDispatch();
  const router=useRouter();

  async function handleLogin({
    username,
    password,
  }: {
    username: string;
    password: string;
  }) {
    try {
      const response = await axios.get(API_URL + "users/login", {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          "X-Requested-With": "XMLHttpRequest",

          Authorization: "Basic " + btoa(`${username}:${password}`),
        },
      });
      if (response.request.status == 200) {
        setMessage(response.data.message);
        dispatch(login({user: response.data.username}));
        setTimeout(() => {
          router.push("/");
        }, 2000);
      }
    } catch (err: any) {
      console.log(err);
      if (err.response && err.response.data) {
        const errorData = err.response.data;
        if (errorData.username) {
          setError(`Username: ${errorData.username}`);
        } else if (errorData.password) {
          setError(`Password: ${errorData.password}`);
        } else {
          setError(err.message);
        }
      } else {
        setError(err.message);
      }
    }
  }

  return (
    <div className="flex justify-center h-max mt-10">
      <div className="bg-white p-6 rounded-lg shadow-md w-80">
        <h1 className="text-lg font-bold text-center mb-4">Login</h1>
        <div className="mb-4 space-y-4">
          <input
            type="text"
            placeholder="Username"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            className="w-full p-2 border border-gray-300 rounded-lg"
          />
          <input
            type="password"
            placeholder="Password"
            className="w-full p-2 border border-gray-300 rounded-lg"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
          />
        </div>

        <button
          onClick={() => {
            handleLogin({ username, password });
          }}
          className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded-lg"
        >
          Login
        </button>
        {message && <p style={{ color: "green" }}>{message}</p>}
        {error && (
          <p className="text-red-500 text-sm mt-2">
            Invalid Username or Password
          </p>
        )}
      </div>
    </div>
  );
};

export default LoginPage;
